<template>
  <div class="group-detail-page">
    <div>
      <div class="header-content">
        <svg-icon icon-class="Group" class="title-img"></svg-icon>
        <div class="titles">
          <div class="title-main">{{ `${basicInfoForm.groupCode || '-'} -- ${basicInfoForm.groupName || '-'}` }}</div>
          <div class="title-light">{{ $t('IDC.groupDetail.label.Status') }} : {{ statusName }}</div>
        </div>
      </div>
      <!-- title -->
      <div class="panel">
        <div class="content">
          <el-tabs v-model="currentPannel" style="min-height: 440px">
            <el-tab-pane
              v-for="(item, index) in [
                $t('IDC.groupDetail.label.info'),
                $t('IDC.groupDetail.label.AuditLog'),
                $t('IDC.groupDetail.label.GroupMembers'),
              ]"
              :key="item + index"
              :label="item"
              :name="item"
            >
              <info
                v-if="currentPannel == $t('IDC.groupDetail.label.info')"
                :basicInfoForm="basicInfoForm"
                :groupInfoDetail="groupInfoDetail"
              ></info>
              <group-members v-if="currentPannel == $t('IDC.groupDetail.label.GroupMembers')"></group-members>
              <audit-log
                v-if="currentPannel == $t('IDC.groupDetail.label.AuditLog')"
                :title="groupAudio"
                :initiatorType="'Group'"
                :identityId="paramObj.identityId"
              ></audit-log>
            </el-tab-pane>
          </el-tabs>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { groupInfo } from '@/views/identifier-center/api/api-group.js';
import info from './info';
import GroupMembers from '../../group/group-members';
import AuditLog from '@/views/identifier-center/pages/organization-operator/detail/audit-log/index.vue';
import { STATUS_MAP } from '@/views/task-center/page/task-detail/utils/data-dic.js';
export default {
  name: 'groupDetail',
  components: {
    info,
    GroupMembers,
    AuditLog,
  },
  provide() {
    return {
      parentView: this,
    };
  },
  data() {
    return {
      groupAudio: {
        title: '',
        desc: this.$t('IDC.auditlog.groupDesc'),
      },
      titleConfig: {
        title: '',
        desc: this.$t('IDC.groupDetail.pageDesc'),
      },
      submitLoading: false,
      basicInfoForm: {},
      groupInfoDetail: {},
      currentPannel: this.$t('IDC.groupDetail.label.info'), // 当前tab的激活项
      paramObj: {},
      statusName: '',
    };
  },
  created() {},
  activated() {
    this.getRouteParam();
  },
  deactivated() {
    this.basicInfoForm = {};
  },
  computed: {
    basePath() {
      const type = this.getConfigRouteType();
      return `/config/${type}/commissionDistribution/`;
    },
  },
  methods: {
    getRouteParam() {
      let params = this.$route.query.params;
      this.paramObj = params && JSON.parse(decodeURIComponent(params));
      this.getDetail();
    },
    async getDetail() {
      const params = {
        identityId: this.paramObj.identityId,
      };
      const res = await groupInfo(params);
      if (res.header && res.header.success) {
        this.groupInfoDetail = res;
        this.basicInfoForm = res.group;
        this.showStatus(this.basicInfoForm.status);
      }
    },

    toReturn() {
      this.$tagTab.closeCurrentPage();
    },
    showStatus(status) {
      this.statusName = STATUS_MAP[status] || '-';
    },
  },
};
</script>

