<template>
  <div class="group-member">
    <!--search -->
    <portal-form
      ref="searchList"
      :form-data="searchList"
      :form-config-list="formConfigList"
      :searchBtnLoading="searchLoading"
      :formRules="queryFormRules"
      @common-enter-event="search"
      @reset-btn-click="reset"
      @search-btn-click="search"
    />
    <!-- columns -->
    <div class="table-list-container">
      <div style="padding-bottom: 20px" v-if="groupDetail && groupDetail.group && groupDetail.group.status != '06'">
        <el-button type="primary" size="small" icon="el-icon-plus" @click="addMember" v-preventReClick>{{
          $t('IDC.groupMembers.AddGroupMember')
        }}</el-button>
      </div>
      <table-page
        size="small"
        :has-pagination="true"
        :columns="columns"
        :pagination="paginationConfig"
        :getTableData="getTableData"
        :is-search="refreshTableFlag"
        :sortArr="['state', 'createTime', 'activeTime']"
      >
        <template slot="status" slot-scope="scope">
          <span :class="`circle status-${scope.data.row.state}`"></span>
          {{ showStatus(scope.data.row.state) }}
        </template>
        <template slot="createTime" slot-scope="scope">
          {{ scope.data.row.createTime ? formatDateFromArray(scope.data.row.createTime, 'DD/MM/YYYY') : '-' }}
        </template>
        <template slot="activeTime" slot-scope="scope">
          {{ scope.data.row.activeTime ? formatDateFromArray(scope.data.row.activeTime, 'DD/MM/YYYY') : '-' }}
        </template>
        <template slot="role" slot-scope="scope">
          <span v-for="(item, index) in scope.data.row.groupMemberRoles" :key="item.roleId">
            {{ item.roleName }} <span v-if="index < scope.data.row.groupMemberRoles.length - 1">,</span>
          </span>
        </template>
        <template slot="operation" slot-scope="scope">
          <el-button type="text" size="small" class="btn" @click="detail(scope.data.row)" v-preventReClick>
            {{ $t('common.label.detail') }}
          </el-button>
          <span class="buttomBar" v-if="scope.data.row.state != '00'">|</span>
          <el-button
            type="text"
            size="small"
            class="btn"
            @click="changeRole(scope.data.row)"
            v-preventReClick
            v-if="scope.data.row.state != '00'"
          >
            {{ $t('IDC.groupMembers.changeRole') }}
          </el-button>
          <span class="buttomBar" v-if="scope.data.row.state != '00'">|</span>
          <el-button
            v-if="scope.data.row.state != '00'"
            type="text"
            size="small"
            class="btn"
            @click="deleteOperation(scope.data.row)"
            v-preventReClick
          >
            {{ $t('common.button.remove') }}
          </el-button>
        </template>
      </table-page>
    </div>
    <template>
      <el-dialog
        :title="$t('IDC.groupMembers.addMember')"
        :visible.sync="dialogAddVisible"
        width="448px"
        :before-close="addCancel"
      >
        <el-form label-position="top" :model="addForm" ref="addForm" :rules="addFormRules">
          <el-row :gutter="20">
            <el-col :span="24">
              <el-form-item :label="$t('IDC.groupMembers.msisdn')" prop="msisdn">
                <el-input size="small" v-model="addForm.msisdn" :placeholder="$t('common.placeholder.enter')">
                </el-input>
              </el-form-item>
            </el-col>
            <el-col :span="24">
              <el-form-item :label="$t('IDC.groupMembers.role')" prop="roleId">
                <el-select size="small" v-model="addForm.roleId" :placeholder="$t('common.placeholder.select')">
                  <el-option v-for="item in roleOptions" :key="item.roleId" :label="item.name" :value="item.roleId">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>

        <span slot="footer" class="dialog-footer">
          <el-button @click="addCancel" size="small" v-preventReClick>{{ $t('common.button.cancel') }}</el-button>
          <el-button type="primary" size="small" @click="addSubmit" v-preventReClick :loading="submitLoading">{{
            $t('common.button.submit')
          }}</el-button>
        </span>
      </el-dialog>
    </template>
    <template>
      <el-dialog
        :title="$t('IDC.groupMembers.changeGroupRole')"
        :visible.sync="dialogChangeVisible"
        width="448px"
        :before-close="changeCancel"
      >
        <el-form label-position="top" :model="changeForm" ref="changeForm" :rules="changeFormRules">
          <el-row :gutter="20">
            <el-col :span="24">
              <el-form-item>
                <p class="change-note">{{ $t('IDC.groupMembers.changeRoleNoTe1') }}</p>
                <p class="change-note">{{ $t('IDC.groupMembers.changeRoleNoTe2') }}</p>
              </el-form-item>
            </el-col>
            <el-col :span="24">
              <el-form-item :label="$t('IDC.groupMembers.role')" prop="role">
                <el-select
                  size="small"
                  v-model="changeForm.role"
                  multiple
                  :placeholder="$t('common.placeholder.select')"
                >
                  <el-option v-for="item in changeOptions" :key="item.roleId" :label="item.name" :value="item.roleId">
                  </el-option>
                </el-select>
              </el-form-item>
            </el-col>
          </el-row>
        </el-form>

        <span slot="footer" class="dialog-footer">
          <el-button @click="changeCancel" size="small" v-preventReClick>{{ $t('common.button.cancel') }}</el-button>
          <el-button type="primary" size="small" @click="changeSubmit" v-preventReClick :loading="submitLoading">{{
            $t('common.button.submit')
          }}</el-button>
        </span>
      </el-dialog>
    </template>
    <template>
      <el-dialog
        :title="$t('IDC.groupMembers.removeGroupMember')"
        :visible.sync="dialogVisible"
        width="448px"
        :before-close="handleClose"
      >
        <div class="remove-title">
          {{ removeTitleTop }}
        </div>
        <div class="note-Title">
          {{ $t('IDC.groupMembers.removeGroupMemberTitle') }}
        </div>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false" size="small" v-preventReClick>{{
            $t('common.button.cancel')
          }}</el-button>
          <el-button type="primary" @click="removeSubmit" size="small" v-preventReClick :loading="submitLoading">{{
            $t('common.button.submit')
          }}</el-button>
        </span>
      </el-dialog>
    </template>
  </div>
</template>

<script>
import TablePage from '@/components/tablePage';
import PortalForm from '@/components/portal-form';
import { formatDateFromArray } from '@/common/utils/date-time-utils';
import {
  inputRequiredValidateObj,
  selectRequiredValidateObj,
  len1To18StrValidateObj,
  len0To18StrValidateObj,
  integerValidateObj,
} from '@/common/validator/common';
import {
  queryGroupMemberList,
  queryGroupMemberRule,
  changeGroupMemberRole,
  addGroupMember,
  groupInfo,
  removeGroupMember,
} from '@/views/identifier-center/api/api-group.js';
export default {
  components: { TablePage, PortalForm },
  name: 'GroupMembers',
  data() {
    return {
      submitLoading: false,
      searchLoading: false,
      refreshTableFlag: false,
      paginationConfig: {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
      },
      searchList: {
        msisdn: '',
        role: '',
        status: '',
      },
      statusList: [
        {
          value: '',
          label: this.$t('IDC.orgCustomer.all'),
        },
        {
          value: '00',
          label: 'Invited',
        },
        {
          value: '01',
          label: 'Confirmed',
        },
        {
          value: '02',
          label: 'Removed',
        },
      ],
      columns: [
        { label: this.$t('IDC.groupMembers.msisdn'), prop: 'groupMemberMSISDN', align: 'left' },
        { label: this.$t('IDC.groupMembers.role'), prop: 'role', align: 'left', __slotName: 'role' },
        { label: this.$t('IDC.groupMembers.status'), prop: 'state', align: 'left', __slotName: 'status' },
        {
          label: this.$t('IDC.groupMembers.creationTime'),
          prop: 'createTime',
          __slotName: 'createTime',
          align: 'left',
        },
        { label: this.$t('IDC.groupMembers.joinTime'), prop: 'activeTime', __slotName: 'activeTime', align: 'left' },
        {
          label: this.$t('IDC.groupMembers.operation'),
          prop: 'operation',
          align: 'left',
          __slotName: 'operation',
          width: '300',
        },
      ],
      addForm: {
        msisdn: '',
        roleId: '',
      },
      changeForm: {
        role: [],
      },
      roleOptions: [],
      changeOptions: [],
      searchRoleOptions: [],
      dialogAddVisible: false,
      dialogVisible: false,
      deleteObj: '',
      dialogChangeVisible: false,
      removeTitleTop: '',
      addFormRules: {
        msisdn: [inputRequiredValidateObj, len1To18StrValidateObj, integerValidateObj],
        roleId: [selectRequiredValidateObj],
      },
      groupDetail: {},
      changeFormRules: {
        role: [selectRequiredValidateObj],
      },
      queryFormRules: {
        msisdn: [integerValidateObj, len0To18StrValidateObj],
      },
      identityId: '',
    };
  },
  computed: {
    formConfigList() {
      return [
        {
          label: this.$t('IDC.groupMembers.msisdn'),
          key: 'msisdn',
          comp: 'el-input',
          bind: {
            clearable: true,
            placeholder: this.$t('common.placeholder.enter'),
          },
        },
        {
          label: this.$t('IDC.groupMembers.role'),
          key: 'role',
          comp: 'el-select',
          options: this.searchRoleOptions,
          bind: {
            placeholder: this.$t('common.placeholder.select'),
          },
        },
        {
          label: this.$t('IDC.groupMembers.status'),
          key: 'status',
          comp: 'el-select',
          options: this.statusList,
          bind: {
            placeholder: this.$t('common.placeholder.select'),
          },
        },
      ];
    },
  },
  created() {
    this.getRouteParam();
  },
  methods: {
    formatDateFromArray,
    getRouteParam() {
      let params = this.$route.query.params;
      this.paramObj = params && JSON.parse(decodeURIComponent(params));
      this.identityId = this.paramObj['identityId'] || '';
      this.groupInfo();
    },
    reset() {
      this.searchList = {
        msisdn: '',
        role: '',
        status: '',
      };
      this.paginationConfig = {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
      };
      this.refreshTableFlag = true;
    },
    search() {
      this.$refs.searchList.validate((valid) => {
        if (valid) {
          this.paginationConfig = {
            total: 0,
            pageIndex: 1,
            pageSize: 10,
          };
          this.refreshTableFlag = true;
        }
      });
    },
    addMember() {
      this.dialogAddVisible = true;
    },
    addCancel() {
      this.dialogAddVisible = false;
      this.addForm = {};
      this.$refs['addForm'].resetFields();
    },
    addSubmit() {
      this.$refs['addForm'].validate(async (valid) => {
        if (valid) {
          const params = {
            invitedMSISDN: this.addForm.msisdn,
            roleId: this.addForm.roleId,
            groupCode: this.groupDetail.group.groupCode,
            groupId: this.identityId,
            receiverIdentityId: this.identityId,
          };
          this.submitLoading = true;
          const res = await addGroupMember(params).finally(() => {
            this.submitLoading = false;
            this.addCancel();
          });
          if (res.header && res.header.success) {
            if (res.taskId) {
              if (res.taskId == '-1') {
                this.$message.success('The request must be approved by group approver.');
              } else {
                this.$message.success('The request must be approved by another operator.');
              }
            } else {
              this.$message.success('Add Group Member Succeed');
            }
            this.reset();
          }
        }
      });
    },
    changeRole(record) {
      this.changeForm.record = record;
      this.dialogChangeVisible = true;
    },
    changeCancel() {
      this.dialogChangeVisible = false;
      this.changeForm.role = [];
      this.$refs['changeForm'].resetFields();
    },
    changeSubmit() {
      this.$refs['changeForm'].validate(async (valid) => {
        if (valid) {
          let addRoleId = this.changeForm.role.map((item) => {
            return {
              roleId: item,
            };
          });
          let removeRoleList = this.changeForm.record.groupMemberRoles.map((item) => {
            return {
              roleId: item.roleId,
            };
          });
          // 去重，需要在addrole 和remove 中的相同数据都删掉
          var loop = addRoleId.length; // 循环次数
          for (let i = 0; i < loop; i++) {
            let duplicateRolesIndex = removeRoleList.findIndex((item) => item.roleId === addRoleId[i].roleId);
            if (duplicateRolesIndex > -1) {
              addRoleId.splice(i, 1);
              removeRoleList.splice(duplicateRolesIndex, 1);
              i = i - 1; // 改变循环变量
              loop = loop - 1; // 改变循环次数
            }
          }
          if (addRoleId.length == 0 && removeRoleList.length == 0) {
            this.changeCancel();
            this.dialogChangeVisible = false;
            this.$modal.msgWarning('Rule profile is the same as before the change.');
            return;
          }
          const params = {
            receiverIdentityId: this.identityId,
            // group member 的id,对应列表接口的identityId字段
            changedId: this.changeForm.record.customerId,
            addRoleList: addRoleId || [],
            removeRoleList: removeRoleList || [],
            changedMSISDN: this.changeForm.record.groupMemberMSISDN,
          };
          this.submitLoading = true;
          const res = await changeGroupMemberRole(params).finally(() => {
            this.submitLoading = false;
            this.changeCancel();
            this.reset();
            this.dialogChangeVisible = false;
          });
          if (res.header && res.header.success) {
            this.dialogChangeVisible = false;
            if (res.taskId) {
              if (res.taskId == '-1') {
                this.$message.success('The request must be approved by group approver.');
              } else {
                this.$message.success('The request must be approved by another operator.');
              }
            } else {
              this.$message.success('Change Member Role Succeed');
            }
          }
        }
      });
    },
    detail(scope) {
      let data = {
        identityID: scope.customerId,
      };
      this.openNewWindow({
        path: '/mainPage/identifierCenter/organizationInfo/customer/detail',
        query: { params: encodeURIComponent(JSON.stringify(data)) },
      });
    },
    deleteOperation(scope) {
      this.dialogVisible = true;
      this.deleteObj = scope;
      this.removeTitleTop = this.$t('IDC.groupMembers.removeTitleTop', {
        obj1: scope.groupMemberMSISDN,
        obj2: this.groupDetail.group.groupName,
      });
    },
    async removeSubmit() {
      let params = {
        groupCode: this.groupDetail.group.groupCode,
        removedId: this.deleteObj.customerId,
        receiverIdentityId: this.identityId,
        removedMSISDN: this.deleteObj.groupMemberMSISDN,
      };
      this.submitLoading = true;
      const res = await removeGroupMember(params).finally(() => {
        this.submitLoading = false;
        this.dialogVisible = false;
        this.reset();
      });
      if (res.header && res.header.success) {
        if (res.taskId) {
          if (res.taskId == '-1') {
            this.$message.success('The request must be approved by group approver.');
          } else {
            this.$message.success('The request must be approved by another operator.');
          }
        } else {
          this.$message.success('Remove Group Member Succeed');
        }
      }
    },
    handleClose(done) {
      done();
    },
    // 获取弹出框表格数据
    async getTableData() {
      const params = {
        pageSize: this.paginationConfig.pageSize,
        pageNo: this.paginationConfig.pageIndex,
        msisdn: this.searchList.msisdn,
        role: this.searchList.role,
        status: this.searchList.status,
        identityId: this.identityId,
      };
      const res = await queryGroupMemberList(params).finally(() => {
        this.refreshTableFlag = false;
      });
      if (res.header && res.header.success) {
        return {
          ...res,
          totalRecords: res.total || 0,
          list: res.groupMemberList || [],
        };
      }
    },
    showStatus(status) {
      let res = this.statusList.filter((item) => {
        return item.value == status;
      });
      return res.length > 0 ? res[0].label : '-';
    },
    async queryGroupMemberRule() {
      let params = {
        productIds: [this.groupDetail.group.groupType],
      };
      const res = await queryGroupMemberRule(params);
      if (res.header && res.header.success) {
        this.searchRoleOptions = res.groupTypeRoles.map((item) => {
          return {
            label: item.name,
            value: item.roleId,
          };
        });
        this.searchRoleOptions.unshift({ label: this.$t('IDC.orgCustomer.all'), value: '' });
        this.changeOptions = res.groupTypeRoles;
        this.roleOptions = res.groupTypeRoles;
      }
    },
    async groupInfo() {
      const params = {
        identityId: this.identityId,
      };
      const res = await groupInfo(params);
      if (res.header && res.header.success) {
        this.groupDetail = res;
        this.queryGroupMemberRule();
      }
    },
  },
};
</script>

<style scoped lang="scss">
.group-member {
  .buttomBar {
    font-size: 16px;
    color: $--fill_color--light;
    padding: 12px;
  }
  .remove-title {
    line-height: 16px;
    color: $--text_color--strong;
  }
  .note-Title {
    line-height: 22px;
    color: $--status_color--secondary-warning;
    background-color: $--status_color--secondary-warning-bg-light;
    font-size: 14px;
    border-radius: 4px;
    margin-top: 20px;
    margin-bottom: 20px;
    padding: 12px;
  }
  .change-note {
    line-height: 22px;
    word-break: normal;
    margin: 0px;
    font-size: 14px;
    color: $--text_color--secondary;
  }
  .circle {
    width: 6px;
    height: 6px;
    border-radius: 50%;
    background-color: $--fill_color--strong;
    display: inline-block;
    margin-right: 4px;
    vertical-align: middle;
  }
  .status-00 {
    background-color: $--primary_color;
  }
  .status-01 {
    background-color: $--status_color--success;
  }
  .status-02 {
    background-color: $--fill_color--strong;
  }
}
</style>
