<template>
  <div class="kyc-info">
    <kyc-form ref="kycFormEdit" mode="edit" :identityId="identityId" identityClassify="group" :adaptiveLayout="true" />
  </div>
</template>

<script>
import KycForm from '@/components/kyc-common/kyc-form/index.vue';
export default {
  name: 'groupDetailKYCInfo',
  components: { KycForm },
  inject: ['parentView'],
  data() {
    return {
      pageType: 'detail',
      submitLoading: false,
      identityId: '',
    };
  },
  created() {
    this.identityId = JSON.parse(decodeURIComponent(this.$route.query.params)).identityId;
  },
  activated() {},
  deactivated() {},
  computed: {
    basePath() {
      const type = this.getConfigRouteType();
      return `/config/${type}/commissionDistribution/`;
    },
  },
  methods: {
    changePageTypeEdit() {
      this.pageType = 'edit';
    },
    reset() {},
    handleSave() {
      this.pageType = 'detail';
    },
  },
};
</script>

