      <div class="basic-info-container">
        <p class="title" style="margin-top: 20px; padding-left: 20px">{{ $t('IDC.groupDetail.label.basicInfo') }}</p>
        <el-row :gutter="20" style="margin-top: 20px; padding-left: 20px; padding-right: 20px">
          <el-col v-bind="colOptions">
