<script>
import KycInfo from './kyc-info';
import dayjs from 'dayjs';
export default {
  name: 'groupDetail',
  components: { KycInfo },
  data() {
    return {
      titleConfig: {
        title: '',
        desc: this.$t('IDC.groupDetail.pageDesc'),
      },
      submitLoading: false,
      currentPannel: this.$t('IDC.groupDetail.label.AccountInfo'), // 当前tab的激活项
    };
  },
  props: ['basicInfoForm', 'groupInfoDetail'],
  filters: {
    formatTime(value) {
      if (!value) {
        return '-';
      }
      return dayjs(dayjs(value).valueOf()).format('DD/MM/YYYY HH:mm:ss');
    },
    obj2str(val) {
      if (!val) {
        return '-';
      }
      let arr = val.map((item) => {
        return item.segmentName;
      });
      return arr.join(',');
    },
  },
  created() {},
  activated() {},
  deactivated() {},
  computed: {
    basePath() {
      const type = this.getConfigRouteType();
      return `/config/${type}/commissionDistribution/`;
    },
  },
  methods: {
    handleEditIdentity() {},
    handleEditGroupName() {},
    handleEditRuleProfile() {},
    handleEditDescription() {},

    /**
     * 返回操作员列表
     */
    toReturn() {
      this.$tagTab.closeCurrentPage();
    },
  },
};
</script>

